  font-size: 5rem;
  font-weight: 500;
}
.servicios .informacion-servicios {
  padding-bottom: 2rem;
}
@media (min-width: 768px) {
  .servicios .informacion-servicios {
    display: flex;
  }
}
.servicios .items {
  border: 2px solid #D4D6C8;
  border-radius: 1rem;
  padding: 2.2rem;
  margin: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
.servicios .items p {
  color: #FFFFFF;
}
.servicios .items h3 {
  color: #FFFFFF;
  font-weight: 500;
}
.servicios .items img {
  width: 30rem;
}

.yo {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.yo h1 {
  color: #FFFFFF;
}
.yo .box-mi {
  display: flex;
  gap: 2rem;
  justify-content: center;
  align-items: center;
  padding-bottom: 2rem;
}
.yo .box-mi p {
  color: #FFFFFF;
  font-weight: 300;
  font-size: 2.3rem;
  border: 1px solid #D4D6C8;
  border-radius: 1rem;
  width: 40%;
  padding: 2.2rem;
}
.yo .box-mi img {
  border: 1px solid #D4D6C8;
  border-radius: 1rem;
  padding: 1rem;
  width: 30rem;
}
/* burbujas */
.burbuja {
  border-radius: 50%;
  background: #181616;
  opacity: 0.3;
  position: absolute;
  bottom: -150;
  animation: burbujas 3s linear infinite;
}

.burbuja:nth-child(1) {
  width: 80px;
  height: 80px;
  left: 5%;
  animation-duration: 3s;
  animation-delay: 3s;
}

.burbuja:nth-child(2) {
  width: 100px;
  height: 100px;
  left: 35%;
  animation-duration: 3s;
  animation-delay: 5s;
}

.burbuja:nth-child(3) {
  width: 20px;
  height: 20px;
  left: 15%;
  animation-duration: 1.5s;
  animation-delay: 7s;
}

.burbuja:nth-child(4) {
  width: 50px;
  height: 50px;
  left: 90%;
  animation-duration: 6s;
  animation-delay: 3s;
}

.burbuja:nth-child(5) {
  width: 70px;
  height: 70px;
  left: 65%;
  animation-duration: 3s;
  animation-delay: 1s;
}

.burbuja:nth-child(6) {
  width: 20px;
  height: 20px;
  left: 50%;
  animation-duration: 4s;
  animation-delay: 5s;
}

.burbuja:nth-child(7) {
  width: 20px;
  height: 20px;
  left: 50%;
  animation-duration: 4s;
  animation-delay: 5s;
}

.burbuja:nth-child(8) {
  width: 100;
  height: 100px;
  left: 52%;
  animation-duration: 5s;
  animation-delay: 5s;
}

.burbuja:nth-child(9) {
  width: 65px;
  height: 65px;
  left: 51%;
  animation-duration: 3s;
  animation-delay: 2s;
}

.burbuja:nth-child(10) {
  width: 40px;
  height: 40px;
  left: 35%;
  animation-duration: 3s;
  animation-delay: 4s;
}

@keyframes burbujas {
  0% {
    bottom: 0;
    opacity: 0;
  }
  30% {
    transform: translateX(30px);
  }
  50% {
    opacity: 0.4;
  }
  100% {
    bottom: 100vh;
    opacity: 0;
  }
}
@keyframes movimiento {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(30px);
  }
  100% {
    transform: translateY(0);
  }
}
